import React, {useRef, useState, useEffect} from "react";
import axios from "axios";

const Main = () => {

  const accesskey = "m8C8xJeSnEdq2PCiTs25yUyslDd7yRmdmj3_8LpAEuA"
  const secretkey = "6wpLM1A8zjiipfnFdkjUiFLfrnt0-UzP73xDtzh4raw"

   const [image, setImage] = useState('/pic')
   const [imagetext, setimagetext ] = useState('headphone')
   const [imagehash, setImagehash]= useState(Date.now())
   const [responseimage, setResponseimage] = useState('')
   let uploadInput = useRef(null)
  
  const handleUploadImage = (ev) => {
    ev.preventDefault();

    const data = new FormData();
    data.append("file", uploadInput.files[0]);
    fetch("/upload", {
      method: "POST",
      body: data,
    }).then((response) => {
      response.json().then((body) => {
        setImage(body.picture);
        setImagehash(Date.now());
        setimagetext(body.result);
      });
    });
  }

  useEffect(() => {
    axios.get(`https://api.unsplash.com/search/photos?query=${imagetext}&client_id=${accesskey}`)
    .then((response) => {
      console.log(response.data);
      setResponseimage(response.data);
    });
  },[imagetext]);


  return (
    <>
    <div>
       <form onSubmit={handleUploadImage}>

          <input
            type="file"
            ref={(ref) => {
              uploadInput = ref;
            }}
            id="input-file"
            name="input-file"
            accept="image/*"
          />
   
           <div>

          <button className="btn-upload">
            Classify 
          </button>

          </div>

          <div class="preview-box"><img src={`${image}?${imagehash}`}/></div>

          <p className="heading">{imagetext && `That's a ${imagetext}`}</p>

        </form>


   
      
      </div>

      <div className="image_results">
      <>
      {responseimage && <img className="image" src={`${responseimage.results[0].urls.small}?${imagehash}`}/>}
      {responseimage && <img className="image" src={`${responseimage.results[1].urls.small}?${imagehash}`}/>}
      {responseimage && <img className="image" src={`${responseimage.results[2].urls.small}?${imagehash}`}/>}
      {responseimage && <img className="image" src={`${responseimage.results[3].urls.small}?${imagehash}`}/>}
      {responseimage && <img className="image" src={`${responseimage.results[4].urls.small}?${imagehash}`}/>}
      {responseimage && <img className="image" src={`${responseimage.results[5].urls.small}?${imagehash}`}/>}
      {responseimage && <img className="image" src={`${responseimage.results[6].urls.small}?${imagehash}`}/>}
      {responseimage && <img className="image" src={`${responseimage.results[7].urls.small}?${imagehash}`}/>}
      {responseimage && <img className="image" src={`${responseimage.results[8].urls.small}?${imagehash}`}/>}
      {responseimage && <img className="image" src={`${responseimage.results[9].urls.small}?${imagehash}`}/>}
      </>
      </div>

</>
  )
}

export default Main

